import React from "react";
import {useNavigate} from "react-router-dom";

/** Data stored globally */
type RadiantContextProps = {
  drawerOpen: boolean;
  setDrawerOpen: (open: boolean) => void;
  navigate: (route: string) => void;
};

/** Contains global data and allows listening to updates */
export const RadiantContext = React.createContext<RadiantContextProps>({} as RadiantContextProps);

/** Allows to pull variables from the context */
export const useRadiantContext = () => React.useContext(RadiantContext);

/** Initializes the global data values */
export const RadiantContextProvider = (props: {children: React.ReactNode}) => {
  const [drawerOpen, setDrawerOpen] = React.useState(false);
  const navigateReact = useNavigate();
  const navigate = (route: string) => {
    navigateReact(route);
    setDrawerOpen(false);
  };
  return <RadiantContext.Provider value={{drawerOpen, setDrawerOpen, navigate}}>{props.children}</RadiantContext.Provider>;
};
