import {alpha, Box, ButtonBase, Card, Stack, Typography, useTheme} from "@mui/material";
import React from "react";
import {ConfigProjectCardWidth, Filler, Spacer, useMobile} from "./Extensions";
import {ProjectData, projectThumbnailUrl} from "../data/ProjectData";
import moment from "moment";
import {useRadiantContext} from "./RadiantContext";
import {ROUTES} from "../RadiantRouter";

/** A centered box with optional background that keeps content an expected width */
export const ProjectCard = (props: {project: ProjectData}) => {
  const theme = useTheme();
  const isMobile = useMobile();
  const context = useRadiantContext();
  const difficultyColor = React.useMemo(() => {
    switch (props.project.difficulty) {
      case "easy":
        return "#75a743";
      case "medium":
        return "#be772b";
      case "hard":
        return "#a53030";
      default:
        return "#4f8fba";
    }
  }, [props]);

  return (
    <Card sx={{boxShadow: theme.shadows[5]}}>
      <ButtonBase onClick={() => context.navigate(ROUTES.Library + "/" + props.project.id)}>
        <Stack spacing={isMobile ? 2 : 3} sx={{
          minWidth: isMobile ? "100%" : ConfigProjectCardWidth,
          maxWidth: isMobile ? "100%" : ConfigProjectCardWidth,
          overflow: "hidden",

          boxShadow: theme.shadows[5],
          borderStyle: "solid",
          borderWidth: 1,
          borderColor: alpha(theme.palette.common.white, 0.15),
          backgroundColor: "#151d28",
          "&:hover": {backgroundColor: "#202e37"},
          transition: "background-color 100ms linear",
        }}>
          <Spacer size={0} />
          <Stack sx={{paddingX: theme.spacing(isMobile ? 2 : 3)}}>
            <Typography align="left" textTransform="uppercase" noWrap>{props.project.game}</Typography>
            <Typography align="left" variant="h2" noWrap>{props.project.song}</Typography>
            <Stack direction="row" spacing={1}>
              <Typography align="left" noWrap textTransform="capitalize" sx={{color: difficultyColor}}>{props.project.difficulty}</Typography>
              <Filler />
              <Typography align="right" noWrap textTransform="capitalize" sx={{color: "#a8b5b2"}}>{moment(props.project.releaseTimestamp).fromNow()}</Typography>
            </Stack>
          </Stack>
          <Box component="img" sx={{
            width: isMobile ? "100%" : ConfigProjectCardWidth - 2,
          }} src={projectThumbnailUrl(props.project)} />
        </Stack>
      </ButtonBase>
    </Card>
  );
};
